/*--------------------------------------------------------------
# Global
--------------------------------------------------------------*/
#page * {
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}

/* fix resizing of page width when scrollbar disappears when woocommerece cart tab is open */
/*html {
  overflow-y: scroll;
}*/
body.woocommerce-cart-tab-is-visible {
	overflow: scroll;
}

body {
	font-size: 16px;
}

.sec-inner {
	max-width: 1170px;
	margin: 0 auto;
	width: 94%;
}

#main {
	padding-top: 0;
	margin-bottom: 0;
}

#primary {
	margin-bottom: 0;
}

/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Posts
--------------------------------------------------------------*/
.single-post header.entry-header {
	display: flex;
	flex-flow: column-reverse;
	margin-bottom: 0;
	border-bottom: none;
}

.single-post header.entry-header .posted-on {
	margin-bottom: .2em;
}

.single-post header.entry-header .entry-title {
	margin-bottom: .4em;
}

.single-post .type-post .entry-meta {
	border-bottom: 1px solid rgba(0, 0, 0, 0.05);
	width: 100% !important;
	display: block;
}

.single-post .entry-meta .author {
	display: none;
}

.single-post .entry-meta .cat-links {
	text-align: left;
	margin-bottom: 1.2em;
}

.single-post .entry-meta .cat-links .label {
	color: inherit !important;
	padding-left: 0;
	font-size: 14px;
	font-weight: initial !important;
}

.single-post .type-post .entry-content {
	width: 100% !important;
	padding-top: 2em;
}

nav#post-navigation {
	border-top: 1px solid lightgray;
}

/*--------------------------------------------------------------
# Widgets
--------------------------------------------------------------*/
#secondary .widget {
	border: 1px solid lightgray;
	padding: 1em;
	margin-bottom: 2em;
}

#secondary .widget .widget-title {
	font-weight: bold;
}

.widget_price_filter .ui-slider .ui-slider-range, .widget_price_filter .ui-slider .ui-slider-handle {
	background-color: #6AC2D6;
}

/*--------------------------------------------------------------
# Footer
--------------------------------------------------------------*/
.site-footer span.gamma.widget-title {
	color: #6AC2D6;
	font-weight: bold;
	border-bottom: none;
	padding-bottom: 0;
	margin-bottom: .5em;
}

.top-row {
	margin-bottom: 2em;
}

.bottom-row img {
	float: left;
	margin-right: 2em;
	max-width: 50px;
}

.widget_nav_menu ul li:before {
	content: "\232A";
	opacity: 1;
}

.block.footer-widget-1 {
	width: 70% !important;
	margin-right: 25px !important;
}

.block.footer-widget-1::after {
	display: block;
	clear: both;
	float: none;
	content: "";
}

div#media_image-5 {
	border-right: 2px solid #f58220;
	padding-right: 25px;
	max-width: 75px;
	float: left;
}

.widget_nav_menu {
	float: left;
	padding-left: 25px;
}

.footer-cred.site-info {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	font-size: 80%;
}

ul#menu-footer-legal-menu {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	margin-left: 1em;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	-ms-flex-flow: row wrap;
	flex-flow: row wrap;
}

ul#menu-footer-legal-menu li {
	margin-left: 1.75em;
}

ul#menu-footer-legal-menu li a {
	color: #6d6d6d;
}

.footer-cred.site-info span {
	/* flex: 1; */
}

