/* Background color change on hover for top level nav items */
a.ubermenu-target.ubermenu-item-layout-text_only:hover {
	background-color: #f58220 !important;
	color: white !important;
}

@media screen and (min-width: 692px) {
	.storefront-primary-navigation {
		-webkit-box-pack: justify;
		-ms-flex-pack: justify;
		justify-content: space-between;
		/* FF3.6-15 */
		background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 25%, white 100%);
		/* Chrome10-25,Safari5.1-6 */
		background: -webkit-gradient(linear, left top, right top, from(rgba(255, 255, 255, 0)), color-stop(25%, rgba(255, 255, 255, 0)), to(white));
		background: -o-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 25%, white 100%);
		background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 25%, white 100%);
	}
	.storefront-primary-navigation a {
		font-weight: bold;
		text-decoration: none;
		font-size: 13px;
		font-size: 1.3rem;
		font-family: 'Open Sans', serif;
	}
}

