	.storefront-primary-navigation a {
		font-weight: bold;
		text-decoration: none;
		font-size: 15px;
		font-size: 1.5rem;
		font-family: 'Open Sans', serif;
	}
}

@media screen and (min-width: 768px) {
	.main-navigation ul.menu > li > a, .main-navigation ul.nav-menu > li > a {
		padding: 1.2em 1em;
	}
}

/*.woocommerce-active .site-header .main-navigation {
    width: 100%;
}*/
.storefront-primary-navigation::before {
	width: 65%;
	height: 0.25rem;
	position: absolute;
	right: 0;
	top: 0;
	content: "";
	background-size: 100%;
	background-image: -webkit-gradient(linear, 0% 50%, 100% 50%, color-stop(0%, rgba(245, 130, 32, 0)), color-stop(40%, rgba(245, 130, 32, 0.8)), color-stop(60%, rgba(245, 130, 32, 0.8)), color-stop(100%, rgba(245, 130, 32, 0)));
	background-image: -webkit-linear-gradient(left, rgba(245, 130, 32, 0), rgba(245, 130, 32, 0.8) 40%, rgba(245, 130, 32, 0.8) 60%, rgba(245, 130, 32, 0));
	background-image: -webkit-gradient(linear, left top, right top, from(rgba(245, 130, 32, 0)), color-stop(40%, rgba(245, 130, 32, 0.8)), color-stop(60%, rgba(245, 130, 32, 0.8)), to(rgba(245, 130, 32, 0)));
	background-image: -o-linear-gradient(left, rgba(245, 130, 32, 0), rgba(245, 130, 32, 0.8) 40%, rgba(245, 130, 32, 0.8) 60%, rgba(245, 130, 32, 0));
	background-image: linear-gradient(to right, rgba(245, 130, 32, 0), rgba(245, 130, 32, 0.8) 40%, rgba(245, 130, 32, 0.8) 60%, rgba(245, 130, 32, 0));
	background-repeat: no-repeat;
}

