/* 
==================================================
## Homepage Featured Menu
==================================================
*/
#menu-homepage {
	display: block;
	-webkit-box-pack: justify;
	-ms-flex-pack: justify;
	justify-content: space-between;
	margin: 0 auto;
	list-style: none;
	max-width: 1100px;
}

.menu-section .sec-inner {
	max-width: 84%;
}

@media screen and (min-width: 540px) {
	#menu-homepage {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
	}
	.menu-section .sec-inner {
		max-width: 1170px;
	}
	#menu-homepage > li {
		margin-bottom: 0;
		margin-top: 0;
	}
}

#menu-homepage > li {
	position: relative;
	-webkit-box-flex: 0;
	-ms-flex: 0 0 31%;
	flex: 0 0 31%;
	-webkit-box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.25);
	box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.25);
	padding: 15px;
	position: relative;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	background-clip: content-box;
	max-width: 100%;
	margin: 8vw auto;
	width: 100%;
}

#menu-homepage > li a {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	padding: 15px;
}

#menu-homepage > li a span {
	position: absolute;
	bottom: 15px;
	left: 15px;
	right: 15px;
	padding: 0;
	background-color: #6bc2d6;
	vertical-align: middle;
	display: table-cell;
	display: table-cell;
	vertical-align: middle;
	font-size: 2.4rem;
	line-height: 2;
	text-align: center;
	color: white;
	font-weight: 600;
	text-transform: uppercase;
	transition: all 0.25s;
}

#menu-homepage > li:hover a span {
	background-color: #347291 !important;
}

@media screen and (min-width: 540px) {
	#menu-homepage {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
	}
	#menu-homepage > li {
		margin: 0;
		width: 31%;
		max-width: 31%;
	}
	#menu-homepage > li a span {
		font-size: 2.2vw;
	}
}

@media screen and (min-width: 1200px) {
	#menu-homepage > li a span {
		font-size: 2.5rem;
	}
}

/* Trick to give 1:1 aspect ration on li without to provide the height */
#menu-homepage li:before {
	content: "";
	display: block;
	padding-top: 100%;
}

ul#menu-homepage a img {
	-webkit-transition: all .3s;
	-o-transition: all .3s;
	transition: all .3s;
	-webkit-filter: brightness(0.93);
	filter: brightness(0.93);
}

ul#menu-homepage a:hover img {
	-webkit-filter: brightness(1);
	filter: brightness(1);
