}

@media screen and (max-width: 767px) {
	section.hero.sec {
		position: relative;
		background-size: cover;
		background-position: 10% center;
	}
	.hero.sec .sec-inner {
		z-index: 1;
	}
	section.hero.sec::after {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		width: 100%;
		background: rgba(0, 0, 0, 0.4);
		z-index: 0;
	}
}

